.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Roster {
  width: 20%;
  margin: 5px;
}

.Roster-table {
  width: 100%;
  margin: 0 auto;
  border: 2px solid;
  /* border-width: 2px solid; */
}

.Selection {
  width: 60%;
}

.team-selectors {
  display: flex;
  justify-content: space-around;
}

.selector-icons {
  height: 475px;
  overflow-y: scroll;
}

.image-text {
  position: relative;
  color: white;
}

.bottom-right {
  /* font-size: large; */
  display: flex;
  position: absolute;
  bottom: 0px;
  line-height: 100%;
  right: 16px;
  text-align: right;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
